.profile-referral-desktop{
    .section-box{
        padding-top: 20px;
        padding-bottom: 20px;
    }
    .referral-link-caption {
      text-align: center;
      font-size: 14px;
      color: #39404d;
      margin-bottom: 18px;
    }
    .referral-banner-wrap {
      background: #fa6e46;
      width: 100%;
      height: 113px;
      border-radius: 5px;
      margin: 0 0 25px;
      position: relative;
      box-sizing: border-box;
      .referral-banner {
        background-image: url(../../../pwa/assets/img/referral/referral-gift.svg);
        width: 71px;
        height: 69px;
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        filter: brightness(0) invert(1);
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
    }

    .referral-link-text {
      p {
        text-align: center;
        color: #e23a3b;
        font-size: 13px;
      }
      .module-border-wrap {
        width: 121px;
        height: 41px;
        position: relative;
        background: linear-gradient(45deg, #fd644f, #f28e26);
        padding: 1px;
        border-radius: 5px;
        margin: 17px auto 13px;
      }

      .module {
        background: #f7f7f7;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 39px;
        text-align: center;
        position: relative;
        span {
          color: #fa6e45;
          font-size: 13px;
          background: linear-gradient(90deg, #fd644f, #f28e26);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
    }

    .referral-qr-code-wrap {
      text-align: center;

      .referral-qr-code-image {
        width: 198px;
        height: 198px;
        background-color: #ffffff;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: auto;
        margin-bottom: 5px;
      }
      .referral-qr-code-text {
        text-align: center;
        font-size: 13px;
        margin-bottom: 46px;
        color: #39404d;
        p {
          margin-bottom: 10px;
        }
        .special {
          color: #fa6e45;
        }
      }
    }

    .referral-tab-caption {
      font-size: 14px;
      color: #39404d;
      margin-bottom: 14px;
    }
    .referral-tab-wrap {
      margin-bottom: 22px;
      .referral-tab-head {
        display: flex;
        flex-wrap: wrap;
        .referral-tab-text {
          flex-grow: 1;
          max-width: 100%;
          height: 42px;
          width: 39%;
          display: table;

          span {
            background: #fa6e46;
            text-align: center;
            padding: 10.5px;
            color: #fff;
            width: 100%;
            border-right: 1px solid #fff;
            font-size: 14px;
            display: table-cell;
            vertical-align: middle;
            @media screen and (max-width: 426px) {
              font-size: 13px;
            }
          }
          &.xs {
            width: 15%;
          }
          &.smaller {
            width: 22%;
            span {
              padding: 10.5px 5px;
            }
          }
          &.medium {
            width: 26%;
          }
          @media screen and (max-width: 425px) {
            height: 58px;
          }
        }
      }
      .referral-tab-body {
        display: flex;
        flex-wrap: wrap;
        .referral-tab-text {
          flex-grow: 1;
          max-width: 100%;
          height: 49px;
          width: 39%;
          display: table;
          span {
            background: #eae9e9;
            text-align: center;
            color: #39404d;
            width: 100%;
            border-right: 1px solid #fff;
            font-size: 12px;
            display: table-cell;
            vertical-align: middle;
          }
          &.special span {
            color: #fa6e46;
            font-size: 14px;
          }
          &.xs {
            width: 15%;
          }
          &.smaller {
            width: 22%;
          }
          &.medium {
            width: 26%;
          }
          &.large {
            width: 100%;
          }
        }
      }
    }
}